import React, { useState , useEffect , useRef  } from "react";
import myStyle from './Main.module.scss';
import BankAccount from './content/BankAccount';
import Report from './content/Report';
import Directions from './content/Directions';


function Main({imageSrc}) {
	const [activeSection, setActiveSection] = useState("Report"); // 기본 섹션을 "schedule"로 설정
	
  return (
		<div className={myStyle.main}>
			<div className={myStyle.title}>
				<div className={myStyle.buttonGroup}>
					<button className={`${myStyle.customButton} ${activeSection === "Report" ? myStyle.selected : ""}`} onClick={() => setActiveSection("Report")} >모시는 <span className={myStyle.point}>글</span></button>
					{/* <span className={myStyle.separator}> / </span> */}
					<button className={`${myStyle.customButton} ${activeSection === "Directions" ? myStyle.selected : ""}`} onClick={() => setActiveSection("Directions")}>오시는 <span className={myStyle.point}>길</span></button>
					{/* <span className={myStyle.separator}> / </span> */}
					<button className={`${myStyle.customButton} ${activeSection === "BankAccount" ? myStyle.selected : ""}`} onClick={() => setActiveSection("BankAccount")}>전하는 <span className={myStyle.point}>곳</span></button>
				</div>
			</div>
			<div className={myStyle.content}>
				{/*  화면 출력 */}
				{activeSection === "Report" && <Report imageSrc={imageSrc}/>}
				{activeSection === "Directions" && <Directions />}
				{activeSection === "BankAccount" && <BankAccount />}
			</div>
    </div>
  );
}

export default Main;
